<template>
    <div class="seller">
      <div class="sellerContent">
        <div class="header">
          <h1 class="title">{{seller.name}}</h1>
          <div class="desc">
            <star :size="36" :score="seller.score"></star>
            <span class="text">({{seller.ratingCount}})</span>
            <span class="text">月售{{seller.sellCount}}单</span>
          </div>
          <ul class="remark">
            <li class="block">
              <h2>起送价</h2>
              <div class="content">
                <span class="stress">{{seller.minPrice}}</span>元
              </div>
            </li>
            <li class="block">
              <h2>商家配送</h2>
              <div class="content">
                <span class="stress">{{seller.deliveryPrice}}</span>元
              </div>
            </li>
            <li class="block">
              <h2>平均配送时间</h2>
              <div class="content">
                <span class="stress">{{seller.deliveryTime}}</span>分钟
              </div>
            </li>
          </ul>
          <div class="favorite" @click="toggleFavorite">
            <span class="icon-favorite" :class="{active:favorite}"></span>
            <span class="text">{{favoriteText}}</span>
          </div>
        </div>
        <split></split>
        <div class="bulletin">
          <h1 class="title">商家公告</h1>
          <div class="content-wrapper">
            <p class="content">{{seller.bulletin}}</p>
          </div>
          <ul v-if="seller.supports" class="supports">
            <li class="supports-item" v-for="(item,index) in seller.supports">
              <span class="icon" :class="classMap[seller.supports[index].type]"></span>
              <span class="text">{{seller.supports[index].description}}</span>
            </li>
          </ul>
        </div>
        <split></split>
        <div class="pics">
          <h1 class="title">商家实景</h1>
          <div class="pic-wrapper">
            <ul class="pic-list">
              <li class="pic-item" v-for="pic in seller.pics">
                <img :src="pic" width="120" height="90" alt="">
              </li>
            </ul>
          </div>
        </div>
        <split></split>
        <div class="info">
          <h1 class="title">商家信息</h1>
          <ul>
            <li class="info-item" v-for="info in seller.infos">{{info}}</li>
          </ul>
        </div>
      </div>
    </div>
</template>

<script>
import star from '../star/star'
import split from '../split/split'

export default {
  props:{
    seller:{
      type: Object
    }
  },
  data(){
    return{
      favorite:(()=>{
        if(!window.localStorage.favorite){
          return false;
        }else{
          let favorite = JSON.parse(localStorage.favorite);
          return favorite || false;
        }
      })()
    }
  },
  computed:{
    favoriteText(){
      return this.favorite ? '已收藏':'收藏';
    }
  },
  created(){
    this.classMap = ['decrease','discount','special','invoice','guarantee']
  },
  methods:{
    toggleFavorite(){
      this.favorite = !this.favorite;
      let storage = window.localStorage;
      storage.favorite = this.favorite;
    }
  },
  components:{
    star,
    split,
  },

}
</script>
<style lang="stylus">
@import "../../common/stylus/mixin.styl"

  .seller
    position absolute
    overflow-y scroll
    left 0
    width 100%
    top 160px
    bottom 50px
    .header
      position relative
      padding 18px
      .title
        margin-bottom 8px
        color rgb(0,0,0)
        line-height 14px
        font-size 14px
      .desc
        padding-bottom 18px
        border-bottom 1px solid rgba(7,17,27,0.1)
        font-size 0
        .star
          display inline-block
          margin-right 8px
          vertical-align top
        .text
          display inline-block
          margin-right 12px
          line-height 18px
          vertical-align top
          font-size 10px
      .remark
        display flex
        padding-top 18px
        .block
          flex 1px
          text-align center
          border-right 1px solid rgba(7,17,27,0.1)
          &:last-child
            border none
          h2
            margin-bottom 4px
            line-height 10px
            font-size 10px
            color rbg(147,153,159)
          .content
            line-height 24px
            font-size 10px
            color rgb(7,17,27)
            .stress
              font-size 24px
      .favorite
        position: absolute
        width 50px
        right 11px
        top 18px
        text-align center
        .icon-favorite
          display block
          margin-bottom 4px
          line-height 24px
          font-size 24px
          color #d4d6d9
          &.active
            color rgb(240,20,20)
        .text
          line-height 10px
          font-size 10px
          color rgb(77,85,93)
    .bulletin
      padding 18px 18px 0 18px
      .title
        margin-bottom 8px
        line-height 14px
        color rgb(7,17,27)
        font-size 14px
      .content-wrapper
        padding 0 12px 16px 12px
        border-bottom 1px solid rgba(7,17,27,0.1)
        .content
          line-height 24px
          font-size 12px
          color rgb(240,20,20)
      .supports
        .supports-item
          padding 16px 12px
          border-bottom 1px solid rbg(7,17,27,0.1)
          font-size 0
          &:last-child
            border-none()
          .icon
            display inline-block
            width 16px
            height 16px
            margin-right 6px
            vertical-align top
            background-size 16px 16px
            background-repeat no-repeat
            &.discount
              bg-image("discount_4")
            &.decrease
              bg-image("decrease_4")
            &.guarantee
              bg-image("guarantee_4")
            &.invoice
              bg-image("invoice_4")
            &.special
              bg-image("special_4")
          .text
            line-height 16px
            font-size 12px
            color rgb(7,17,27)
    .pics
      padding 18px
      .title
        margin-bottom 8px
        line-height 14px
        color rgb(7,17,27)
        font-size 14px
      .pic-wrapper
        width 100%
        overflow hidden
        white-space nowrap
        overflow-x scroll
        .pic-list
          font-size 0
          .pic-item
            display inline-block
            margin-right 6px
            width 120px
            height 90px
            &:last-child
              margin 0
    .info
      margin 18px 18px 0 18px
      .title
        padding-bottom 12px
        line-height 14px
        border-1px(rgba(7,17,27,0.1))
        color rgb(7,17,27)
        font-size 14px
      .info-item
        padding 16px 12px
        line-height 16px
        border-1px(rgba(7,17,27,0.1))
        font-size 12px
        &:last-child
          border-none()
</style>
